<template>
	<div id="read" :style="readsetting">
		<header class="header"  v-if="readset_flag">
            <router-link to="/detail">
               <i class="iconfont icon-zuo"></i>
            </router-link>
     	</header>
		<div class="read_mask" @click="closeset" v-if="readset_flag" @touchmove.prevent></div>
		<h3 class="chapter_name"> </h3>
		<div class="read_content" v-html="bookcontent" @click="readset()">
			{{bookcontent}}
		</div>
		<div class="prev_next">
			<div class="prev"></div>
			<div class="catelog" @click="toChapterMenu()"></div>
			<div class="next"></div>
		</div>
		
        <div id="set-all" v-if="fontset_flag" >
            <div class="fs">
              <div class="minus" @click="minus(readsetting.fontSize)"><i class="iconfont icon-zitijian"></i></div>
              <div class="add" @click="add(readsetting.fontSize)"><i class="iconfont icon-zitijia"></i></div>
            </div>
            <div class="bg">
              <div v-for=" (item,index) in bgarr" :class="bgnum==index?'bgcur ':''" @click="changebg(index,item)" :style="'background:'+item" >
              	<i :class="bgnum==index?'iconfont icon-queren ':''"></i>
              </div>
            </div>
        </div>
        
		<footer class="footer"  v-if="readset_flag" >
		    <div class="item"  @click="toChapterMenu()" >
		        <i class="iconfont icon-mulu1"></i>
		        <div class="label"></div>
		    </div>
		    <div class="item"  @click="fontset()">
		        <i class="iconfont icon-shezhi" ></i>
		        <div class="label"></div>
		    </div>
		    <div class="item"  @click="setNight()" >
		        <i class="iconfont" :class="!msflag?'icon-yejianmoshi':'icon-rijianmoshi'"></i>
		        <div class="label">{{!msflag?'':''}}</div>
		    </div>
		    <div class="item" @click="addstore()" >
		        <i class="iconfont icon-shoucang" ></i>
		        <div class="label" ></div>
		    </div>
        </footer>
	</div>
</template>
`	
<script>
	export default{
		name:"read",
		data(){
			return{
				bookcontent:'<span>.</span></br><span></span></br><span></span></br><span></span></br><span></span></br><span>.</span></br><span></span></br><span></span></br><span></span></br><span>',
				readset_flag:false, 
				readsetting:{
					fontSize:0.6+"rem",
					background:"#f7e3c2",
					color:"#333"
				},
				bgarr:["#f7e3c2","#cad9e8","#d1edd1","#FCEFFF"],
				bgnum:0,
				fontset_flag:false,
				msflag:null //
			}
		},
		created:function(){
			
			if(localStorage.getItem("readsetting")){
				let readsetting = localStorage.getItem("readsetting")
				this.readsetting = JSON.parse(readsetting)
				if(readsetting.background == "#212121"){
					this.msflag = true
				}else{
					this.msflag = false
				}
			}else{
				this.readsetting = {
					fontSize:0.6+"rem",
					background:"#f7e3c2",
					color:"#333"
				}
				this.msflag = false
			}
			
		},	
		mounted: function(){
			 
		},

		components:{
		
		},
		methods:{
			readset(){
				this.readset_flag = true
			},
			closeset(){
				this.readset_flag = false
				this.fontset_flag = false
				localStorage.setItem('readsetting',JSON.stringify(this.readsetting))
			},
			toChapterMenu(){
				
			},
			fontset(){
				this.fontset_flag = true
			},
			setNight(){
				  if (!this.msflag) { //
                  this.msflag = !this.msflag
                  this.readsetting.background = "#212121"
                  this.readsetting.color = "#eee"
                  localStorage.setItem('readsetting',JSON.stringify(this.readsetting)) 
                } else {
                  this.msflag = !this.msflag
                  this.readsetting.background = "#f7e3c2"
                  this.readsetting.color = "#333"
                  localStorage.setItem('readsetting',JSON.stringify(this.readsetting))
                }
			},
			addstore(){
				
			},
			minus(size){
				var size = parseFloat(size)
				if( size > 0.48){
					this.readsetting.fontSize = size - 0.04 + "rem"
				}else{
					this.readsetting.fontSize = 0.48 + "rem"
				}
			},
			add(size){
				var size = parseFloat(size)
				if( size < 0.96){
					this.readsetting.fontSize = size + 0.04 + "rem"
				}else{
					this.readsetting.fontSize = 0.96 + "rem"
				}
			},
			changebg(index,item){
				this.bgnum = index
				this.readsetting.background = item
			}
		}
	}
</script>

<style lang="scss">
	@import "@/assets/scss/theme.scss";
	@function rf($px){
            @return $px / 50px * 1rem;
        }
        body{
        	height: 100%;
        	flex: 1;
        	
        }
    #read{
    	display: flex;
    	height: 100%;
    	flex-direction: column;
    	justify-content: space-between;
    	padding-top: 0.4rem 0.8rem 0 0.8rem;
    	min-height: 30rem;
    	.read_mask{
		    position: fixed;
		    width: 100%;
		    height: 100%;
		    top: 0;
		    left: 0;
		    background: rgba(0, 0, 0, 0.8);
		    opacity: 0.1;
		    z-index: 99;
		    transition: opacity .3s,transform .3s;
    	}
    	h3{
    		height: 2rem;
    		text-align: center;
    		font-size: 0.66rem;
    		line-height: 2rem;
    	}
    	.read_content{
    		padding: 0 0.8rem ;
    		height: 100%;
    		padding-top: 0.5rem;
    		span{
    			display: block;
    			text-indent: 2em;
    		}
    	}
    	.prev_next{
    		height: 2.6rem;
    		width: 100%;
    		display: flex;
    		align-items: center;
    		justify-content: space-between;
    		padding: 0 1rem;
    		div{
    			height: 1.4rem;
    			width: 25%;
    			display: flex;
    			align-items: center;
    			justify-content: center;
    			border: 1px solid #fbab80;
    			border-radius: 4%;
    			color: #fbab80;
    			font-size: 0.6rem;
    		}
    		.prev{
    			width: 30%;
    		}
    		.catelog{
    			margin: 0;
    		}
    		.next{
    			width: 30%;
    		}
    	}
    	.header{
    		display: flex;
		    height: 1.8rem;
		    /*border-bottom: 1px solid #f0f1f2;*/
			width: 100%;
		    position:fixed; top:0; 
		    background-color: rgba(0, 0, 0, 0.9);
		    z-index:9999;
		    color: #fff;
		    padding: 0 0.8rem;
		    a{
		    	display: block;
		    	i{
		    		height: 1.8rem;
			    	width: 1.8rem;
			    	display: flex;
			    	align-items: center;
			    	justify-content: flex-start;
			    	color: #fff;
			    	font-size: 0.6rem;
		    	}
		    }
    	}
    	.footer{
    		position: fixed;
    		bottom: 0;
			height: 2rem;
			display: flex;
			align-items: center;
			justify-content: space-around;
			background-color: rgba(0, 0, 0, 0.9);
		    color: #fff;
		    border-bottom: 0;
		    transition: opacity .15s,transform .15s,visibility .15s;
		    width: 100%;
		    z-index:999; 
		    .item{
		    	height: 2rem;
		    	width: 20%;
		    	padding: 0.15rem 0;
		    	display: flex;
		    	flex-direction: column;
		    	align-items: center;
		    	justify-content: space-around;
		    	i{
		    		font-size: 0.55rem;
		    	}
		    	.label{
		    		font-size: 0.5rem;
		    	}
		    }
		}
  		#set-all{
	        height: 5.5rem;
	        width: 100%;
	        background: #121212;
	        z-index: 9999;
	        position: fixed;
	        bottom: 2rem;
	        opacity: .9;
	        display: flex;
	        flex-direction: column;
	        justify-content: space-between;
	        color: white;
            .fs{
	            height: 2.5rem;
	            display: flex;
	            align-items: flex-end;
	            justify-content: space-around;
	            div{
		          
		          border: 1px solid white;

		          i{
		          	display: block;
		          	height: 1.8rem;
			        width: 1.8rem;
			        font-size: 0.8rem;
			        line-height: 2rem;
			        text-align: center;
		          }
		        }
        	}
        	.bg{
	            height: 2.5rem;
	            display: flex;
	            align-items: center;
	            justify-content: space-around;
	            .bgcur{
		           border: 1px solid $color;
		        }
		        div{
		          i{
		          	display: block;
		          	color: $color;
		          	font-size: 0.6rem;
		          	height: 1.6rem;
		          	width: 2rem;
		          	text-align: center;
		          	line-height: 1.6rem;
		          }
		        }
	        }
        }
    }    
</style>


