<template>
	<div id="recharge">
        <v-header><slot></slot></v-header>
        <div class="container">
        	<div class="top">
        		<div class="name">APPMkhYSKL</div>
        		<div class="balance">
        			:<span>4890</span>
				</div>
        	</div>
        	<div class="tabbar">
        		<div :class="tabnum==index?'tab_cur':''" v-for="(item,index) in tablist" @click="ctab(index)">
        			<i :class="item.icon"></i>
        			<span>{{item.ti}}</span>
        		</div>
        	</div>
        	<!---->
        	<div class="coinbox">
        		<div>
        			<span>30</span>
        			<span>3000</span>
        			<span>50</span>
        			<span class="hot"></span>
        		</div>
        		<div>
        			<span>30</span>
        			<span>3000</span>
        			<span>50</span>
        		</div>
        		<div>
        			<span>30</span>
        			<span>3000</span>
        			<span>50</span>
        		</div>
        		<div>
        			<span>30</span>
        			<span>3000</span>
        			<span>50</span>
        		</div>
        		<div>
        			<span>30</span>
        			<span>3000</span>
        			<span>50</span>
        		</div>
        		<div>
        			<span>30</span>
        			<span>3000</span>
        			<span>50</span>
        		</div>
        	</div>
        	<!---->
        	<div class="vipbox">
        		<div>
        			<span>30</span>
        			<span>3000</span>
        			<span>50</span>
        		</div>
        		<div>
        			<span>30</span>
        			<span>3000</span>
        			<span>50</span>
        		</div>
        		<div>
        			<span>30</span>
        			<span>3000</span>
        			<span>50</span>
        		</div>
        	</div>
        	
        	<!---->
        	<div class="payway">
        		<div>
        			<img src="@/assets/images/ali.png"/>
        			<span></span>
        		</div>
        		<div>
        			<img src="@/assets/images/wx.png"/>
        			<span></span>
        		</div>
        	</div>
        	
        	<div class="topay">
        		<div></div>
        	</div>
        	
		</div>	
		<!---->
		<div class="remind">
			<div>
				<h4></h4>
	            <p>1 1=100</p>
	            <p>210</p>
	            <p>3	<a></a></p>	
			</div>

     	</div>
	</div>
</template>

<script>
	import vHeader from "@/components/common/v_header"	
	export default{
		name:"rank",
		data(){
			return{
				tabnum:0,
				tablist:[
					{
						ti:"",
						icon:"iconfont icon-qian"	
					},
					{
						ti:"",
						icon:"iconfont icon-huiyuan"	
					},]
			}
		},
		components:{
		 vHeader
		},
		methods:{
			ctab(index){
				this.tabnum = index
			}
		}
	}
</script>

<style lang="scss">
	@import "@/assets/scss/theme.scss";
	@function rf($px){
            @return $px / 50px * 1rem;
        }
    #recharge{
        .container{
        	.top{
        		height: 2rem;
        		padding: 0 0.9rem;
        		display: flex;
        		font-size: 0.6rem;
        		align-items: center;
        		justify-content: flex-start;
        		
        		.name{
        			padding: 0 0.5rem;
        			height: 0.88rem;
        			line-height: 0.88rem;
        			border-left: 0.1rem solid $color;
        			color: $color;
        			font-weight: 600;
        		}
        		.balance{
        			span{
        				color: $color;
        				font-weight: 600;
        				padding: 0 0.2rem;
        			}
        		}
        	}
        	.tabbar{
        		padding: 0 2rem;
        		height: 1.6rem;
        		display: flex;
        		justify-content: space-around;
        		align-items: center;
        		font-size: 0.66rem;
        		.tab_cur{
        			color: $color;
        		}
        		div{
        			display: flex;
        			align-items: center;
        			i{
        				font-size: 0.6rem;
        				display: block;
        				height: 1.6rem;
        				line-height: 1.6rem;
        			}
        			span{
        				display: block;
        				height: 1.6rem;
        				line-height: 1.6rem;
        				padding-left: 0.25rem;
        			}
        		}
        	}
        	.coinbox{
        		padding: 0 0.5rem;
        		display: flex;
        		flex-wrap: wrap;
        		justify-content: space-around;
        		font-size: 0.6rem;
        		div{
        			border: 1px solid #F2F2F2;
        			border-radius: rf(12px);
        			padding: 0.18rem 0;
        			margin-top: 0.6rem;
        			height: 3rem;
        			width: 44%;
        			display: flex;
        			flex-direction: column;
        			justify-content: space-between;
        			text-align: center;
        			position: relative;
        			.hot{
        				font-size: 0.5rem;
        				position: absolute;
        				display: flex;
        				align-items: center;
        				justify-content: center;
        				height: 0.8rem;
        				width: 0.8rem;
        				background: $color;
        				color: white;
        				border-radius: 50%;
        				right: -0.28rem;
        				top: -0.28rem;
        			}
        		}
        	}
        	.vipbox{
        		padding: 0 0.5rem;
        		display: flex;
        		flex-wrap: wrap;
        		justify-content: flex-start;
        		font-size: 0.6rem;
        		div{
        			border: 1px solid #F2F2F2;
        			border-radius: rf(12px);
        			padding: 0.18rem 0;
        			margin-top: 0.6rem;
        			height: 3rem;
        			width: 44%;
        			margin: 3%;
        			display: flex;
        			flex-direction: column;
        			justify-content: space-between;
        			text-align: center;
        		}
        	}
        	.payway{
        		padding: 0 0.5rem;
        		display: flex;
        		justify-content: space-around;
        		padding: 0.5rem 0.8rem;
        		height:4.6rem;
        		div{
        			display: flex;
        			flex-direction: column;
        			width: 4.6rem;
        			justify-content: space-around;
        			align-items: center;
        			font-size: 0.6rem;
        			img{
        				height: 2rem;
        				width: 2rem;
        			}
        		}
        	}
        	.topay{
        		margin: 0.5rem 0;
        		height: 1.8rem;
        		font-size: 0.8rem;
        		color: white;
        		display: flex;
        		justify-content: center;
        		align-items: center;
        		div{
        			width: 12rem;
        			height: 1.6rem;
        			text-align: center;
        			line-height: 1.6rem;
        			border-radius: .9rem;
        			color: white;
        			background: $color;
        		}
        	}
        }
        
        .remind{
        	padding: 0.5rem;
        	div{
        		height: 8rem;
        		border: 1px solid #F2F2F2;
        		padding: 0.4rem;
        		h4{
	        		font-weight: 500;
	        		font-size: 0.7rem;
	        		padding-bottom: 0.2rem;
        		}
        		p{
        			display: block;
        			font-size: 0.6rem;
        			padding: 0.1rem 0 0.1rem 0.6rem;
        			a{
        				color: $color;
        				display: inline-block;
        			}
        		}
        	}
        	
        }
    }    
</style>


